.LoginPageBox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  .formWrapBox {
    width: 300px;
    color: #222222;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 6px;
    overflow: hidden;
    .formBoxFooter {
      width: 100%;
      display: flex;
      justify-content: space-around;
      border-top: 2px solid #ccc;
      .footerBtn {
        height: 40px;
        line-height: 40px;
        flex: 1;
        text-align: center;
        font-size: 14px;
        &:hover {
          cursor: pointer;
        }
      }
      .footerBtn.currentBtn {
        background: #fc6423;
        color: #fff;
        font-size: 16px;
      }
    }
  }
}
